.quill-container p {
    word-break: break-word;
}
details summary::-webkit-details-marker {
    display:none !important;
}
details > summary {
    list-style: none;
}
.btn{
    display: inline-flex;
    cursor: pointer;
    align-items: center;
    justify-content: center;
    border-radius: 0.25rem;
    font-weight: 400;
    font-size: 0.8rem;
    --tw-shadow: 0 1px 2px 0 rgb(0 0 0 / 0.05);
    --tw-shadow-colored: 0 1px 2px 0 var(--tw-shadow-color);
    box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000), var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow);
    transition-property: color, background-color, fill, stroke, opacity, box-shadow, transform, filter, -webkit-text-decoration-color, -webkit-backdrop-filter;
    transition-property: color, background-color, text-decoration-color, fill, stroke, opacity, box-shadow, transform, filter, backdrop-filter;
    transition-property: color, background-color, text-decoration-color, fill, stroke, opacity, box-shadow, transform, filter, backdrop-filter, -webkit-text-decoration-color, -webkit-backdrop-filter;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    transition-duration: 200ms;
    letter-spacing: 0.025em;
    color: #6f6f6f;
    background-color: #fff;
}
.btn svg {
    margin-top: -1px;
}
.dark .btn {
    transition-property: none;
    background-color: #1d1d1d;
    color: #ececec;
    border-color: transparent;
}
.btn:hover:not(:disabled){
    --tw-border-opacity: 0.9;
    --tw-bg-opacity: 0.9;
}
.btn:not(button){
    text-align: center;
}
.btn:disabled{
    cursor: not-allowed;
    opacity: 0.7;
}
/* Default Sizes */
.btn-sm{
    padding-top: 0.375rem;
    padding-bottom: 0.375rem;
    padding-left: 1rem;
    padding-right: 1rem;
    font-size: 0.8rem;
    line-height: 1rem;
}
.btn-lg{
    padding-top: 0.375rem;
    padding-bottom: 0.375rem;
    padding-left: 1rem;
    padding-right: 1rem;
    font-size: 1.125rem;
    line-height: 1.75rem;
}
.btn:focus:not(:disabled), .btn:hover:not(:disabled) {
    --tw-ring-offset-shadow: var(--tw-ring-inset) 0 0 0 var(--tw-ring-offset-width) var(--tw-ring-offset-color);
    --tw-ring-shadow: var(--tw-ring-inset) 0 0 0 calc(2px + var(--tw-ring-offset-width)) var(--tw-ring-color);
    box-shadow: var(--tw-ring-offset-shadow), var(--tw-ring-shadow), var(--tw-shadow, 0 0 #0000);
}
.table{
    width: 100%;
    text-align: left;
}
.table thead.table-dark{
    --tw-bg-opacity: 1;
    background-color: rgb(var(--color-dark) / var(--tw-bg-opacity));
    --tw-text-opacity: 1;
    color: rgb(255 255 255 / var(--tw-text-opacity));
}
.dark .table thead.table-dark{
    background-color: rgb(0 0 0 / 0.3);
}
.table thead.table-dark tr th{
    border-bottom-width: 0px;
}
.table thead.table-light{
    background-color: rgb(var(--color-slate-200) / 0.6);
}
.dark .table thead.table-light{
    --tw-bg-opacity: 1;
    background-color: rgb(var(--color-slate-200) / var(--tw-bg-opacity));
}
.table thead.table-light tr th{
    border-bottom-width: 0px;
    --tw-text-opacity: 1;
    color: rgb(var(--color-slate-700) / var(--tw-text-opacity));
}
.table th {
    border-bottom-width: 2px;
    padding-left: 1.25rem;
    padding-right: 1.25rem;
    padding-top: 1.25rem;
    padding-bottom: 1.25rem;
    font-weight: 700;
    font-size: 0.75rem/* 12px */;
    line-height: 1rem/* 16px */;
    text-transform: uppercase;
    letter-spacing: 0.025em;
    white-space: nowrap;
    color: #7d7d7d;
    background-color: #e9e9e9;
}
.dark .table th {
    color: #bbbbbb;
    background-color: #4d4d55;
}
.table th:first-child {
    border-top-left-radius: 0.375rem;
    border-bottom-left-radius: 0.375rem;
}
.table th:last-child {
    border-top-right-radius: 0.375rem;
    border-bottom-right-radius: 0.375rem;
}
.table td{
    border-color: #f2f2f2;
    border-top-width: 1px;
    border-bottom-width: 1px;
    padding-left: 1.25rem;
    padding-right: 1.25rem;
    padding-top: 0.75rem;
    padding-bottom: 0.75rem;
}
.dark .table td{
    --tw-border-opacity: 1;
    border-color: rgb(var(--color-darkmode-300) / var(--tw-border-opacity));
}
.table.table-dark{
    --tw-bg-opacity: 1;
    background-color: rgb(var(--color-dark) / var(--tw-bg-opacity));
    --tw-text-opacity: 1;
    color: rgb(255 255 255 / var(--tw-text-opacity));
}
.dark .table.table-dark{
    background-color: rgb(0 0 0 / 0.3);
}
.table.table-dark th{
    --tw-border-opacity: 1;
    border-color: rgb(var(--color-slate-600) / var(--tw-border-opacity));
}
.dark .table.table-dark th{
    --tw-border-opacity: 1;
    border-color: rgb(var(--color-darkmode-300) / var(--tw-border-opacity));
}
.table.table-dark td{
    --tw-border-opacity: 1;
    border-color: rgb(var(--color-slate-600) / var(--tw-border-opacity));
}
.dark .table.table-dark td{
    --tw-border-opacity: 1;
    border-color: rgb(var(--color-darkmode-300) / var(--tw-border-opacity));
}
